@import "../../node_modules/bootstrap/scss/_functions";
@import "theme/functions";

@import "../../node_modules/bootstrap/scss/mixins";
@import "theme/mixins";

@import "user-variables";
@import 'theme/colors';
@import "theme/variables";
@import "../../node_modules/bootstrap/scss/variables";
@import "../../node_modules/bootstrap/scss/variables-dark";

/* ========================================================================================*/
$text-light-blue: #27BCFE;
$bg-light-blue: #E5F7FF;
$text-light-orange: #F68F57;
$bg-light-orange: #FEF0E8;

.text-light-blue {
  color: $text-light-blue;
}
.bg-light-blue {
  background-color: $bg-light-blue;
}
.text-light-orange {
  color: $text-light-orange;
}
.bg-light-orange {
  background-color: $bg-light-orange;
}
.card {
    .card-body{
        &.dash-statics{
            padding-top: 0.6rem;
            padding-bottom: 0.6rem;
        }
    }
}
.dash-system-tables table td {
  .badge {
    width: 75px !important;
    height: 23px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.gap-actions {
  .btn {
    height: 36px;
    min-width: 130px;
    font-weight: 400;
    font-size: 0.8rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.form-check-input {
  &-urgent-tasks:checked {
    border-color: $info !important;
    background-color: $info !important;
  }
  &-high-tasks:checked {
    border-color: #2aceff !important;
    background-color: #2aceff !important;
  }
  &-medium-tasks:checked {
    border-color: #73D3FE !important;
    background-color: #73D3FE !important;
  }
  &-low-tasks:checked {
    border-color: #A9E4FF !important;
    background-color: #A9E4FF !important;
  }
}

.qipobjective-header {
  text-align: start !important;
  width: 100%;
  margin: 0 !important;
  height: 50px;
  &:hover {
    box-shadow: none !important;
  }
}
.objective-activities {
  .card-header {
    background-color: #F9FAFF;
    color: #344050;
    padding: 0.8rem 1rem;
    h6 {
      font-size: 1rem;
    }
  }
  .card-footer {
    .btn {
      height: 45px;
    }
  }
  .table {
    td, th {
    text-align: center;
  }
  }
}